import * as React from "react";
import Box from "@mui/material/Box";
import { styled } from "@mui/material/styles";
import {
  ContactEmailIconBaloon as MailToBtn,
  ContactPhoneIconBaloon as PhoneToBtn,
  ContactMapIconBaloon as MapToBtn
} from "../Buttons/contactButtons";
import {
  SocialFacebook as FacebookBtn,
  SocialInstagram as InstagramBtn,
  SocialWhatsapp as WhatsappBtn
} from "../Buttons/socialLinks";
import useMediaQuery from "@mui/material/useMediaQuery";

const OuterWrapper = styled(Box)(({ theme }) => ({
  width: "100%",
  height: "4rem",
  minHeight: "1rem",
  display: "flex",
  flexDirection: "row",
  justifyContent: "space-between",
  alignItems: "center",
  color: theme.text.secondary.main,
  backgroundColor: theme.palette.white.main,
  overflow: "hidden",
  paddingTop: theme.spacing(3),
  paddingBottom: theme.spacing(3),
  paddingLeft: theme.spacing(2),
  paddingRight: theme.spacing(2),
  boxSizing: "border-box"
}));

const InnerWrapper = styled(Box)(({ theme }) => ({
  width: "100%",
  height: "0px",
  display: "flex",
  flexDirection: "row",
  justifyContent: "space-between",
  alignItems: "center",
  color: theme.text.secondary.main,
  backgroundColor: theme.palette.secondary.main,
  borderRadius: "2rem",
  overflow: "hidden",
  paddingTop: theme.spacing(3),
  paddingBottom: theme.spacing(3),
  paddingLeft: theme.spacing(2),
  paddingRight: theme.spacing(2)
}));

const LeftBlock = styled(Box)(({ theme }) => ({
  marginLeft: theme.spacing(1),
  display: "flex",
  flexDirection: "row",
  justifyContent: "center"
}));

const RightBlock = styled(Box)(({ theme }) => ({
  marginRight: theme.spacing(1),
  display: "flex",
  flexDirection: "row",
  justifyContent: "center"
}));

const Header = ({ title }) => {
  const isSm = useMediaQuery((theme) => theme.breakpoints.down("sm"));
  const isMs = useMediaQuery((theme) => theme.breakpoints.down("ms"));
  const leftChildrenProps = {
    label: isSm ? "" : undefined,
    labelFontSize: isMs ? 11 : undefined,
    sx: {
      gap: isSm ? 0 : 1,
      maxWidth: isSm ? 0.5 : 1,
      pr: isSm ? 0 : 1,
      py: isSm ? 0.5 : 0.1,
    }
  };
  if (isSm) leftChildrenProps.sx["& .MuiTouchRipple-root"] = {
    m: 3,
  }
  return (
    <OuterWrapper>
      <InnerWrapper>
        <LeftBlock>
          <MailToBtn variant="secondary" {...leftChildrenProps} />
          <PhoneToBtn variant="secondary" {...leftChildrenProps} />
          <MapToBtn variant="secondary" {...leftChildrenProps} />
        </LeftBlock>
        <RightBlock>
          <WhatsappBtn variant="secondary" />
          <FacebookBtn variant="secondary" />
          <InstagramBtn variant="secondary" />
        </RightBlock>
      </InnerWrapper>
    </OuterWrapper>
  );
};

export default Header;
