import * as React from "react";
import { Box, Container } from "@mui/material";
import { styled } from "@mui/material/styles";
import Header from "../components/Header/Header";
import Footer from "../components/Header/Footer";
import CustomScrollbar from "../components/CustomScrollbar";
import GlobalThemeProvider from "./GlobalThemeProvider";
import PageTransition from '../components/PageTransition';

const Wrapper = styled(Box)(({ theme }) => ({
  overflow: "hidden",
  height: "100vh",
  width: "100vw",
  color: theme.palette.secondary.main
}));

const FooterSection = styled(Box)(({ theme }) => ({
  marginTop: "1rem",
  width: "100%",
  backgroundColor: theme.palette.secondary.main,
  color: theme.text.secondary.main
}));

function Content({ children }) {
  return (
    <Container component="main" maxWidth={false} sx={{ textAlign: "center" }}>
      {children}
    </Container>
  );
}
export default function PageWithHeaderAndFooter({ disableTransition, children }) {
  return (
    <GlobalThemeProvider>
      <Wrapper>
        <Header />
        <CustomScrollbar
          renderTrackVertical={(props) => (
            <div {...props} className="track-vertical" />
          )}
          renderThumbVertical={(props) => (
            <div {...props} className="thumb-vertical" />
          )}
          autoHide
          universal
        >
          {!!disableTransition ? (
            <Content>{children}</Content>
          ) : (
            <PageTransition>
              <Content>{children}</Content>
            </PageTransition>
          )}
          <FooterSection component="section" id="contacts">
            <Footer />
          </FooterSection>
        </CustomScrollbar>
      </Wrapper>
    </GlobalThemeProvider>
  );
}
