import * as React from "react";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import CommonButton from "../common/CommonButton/CommonButton";
import { Link as RouterLink } from "gatsby";
import { styled } from "@mui/material/styles";
import {
  ContactEmailIconBaloon as MailToBtn,
  ContactPhoneIconBaloon as PhoneToBtn,
  ContactMapIconBaloon as MapToBtn,
  ContactWhatsappIconBaloon as WhatsappToBtn
} from "../Buttons/contactButtons";
import {
  SocialFacebook as FacebookBtn,
  SocialInstagram as InstagramBtn
} from "../Buttons/socialLinks";
import { DenteLogoIcon } from "../common/icons";
import { Client, pagesLinks as links } from "../../globals";
import { useTheme } from "@mui/material/styles";

const OuterWrapper = styled(Box)(({ theme }) => ({
  width: "100%",
  minHeight: "1rem",
  display: "flex",
  flexDirection: "row",
  justifyContent: "space-between",
  alignItems: "center",
  color: theme.text.secondary.main,
  backgroundColor: theme.palette.white.main,
  overflow: "hidden",
  paddingTop: theme.spacing(3),
  paddingBottom: theme.spacing(3),
  paddingLeft: theme.spacing(2),
  paddingRight: theme.spacing(2),
  boxSizing: "border-box"
}));

const InnerWrapper = styled(Grid)(({ theme, sx }) => ({
  width: "100%",
  // height: "100px",
  color: theme.text.secondary.main,
  backgroundColor: theme.palette.secondary.main,
  borderRadius: "1rem",
  overflow: "hidden",
  paddingTop: theme.spacing(3),
  paddingBottom: theme.spacing(3),
  "& span, & p": { fontSize: "0.85rem" },
  ...sx
}));

const MiniTitle = styled(Typography)(({ theme }) => ({
  fontWeight: "bold"
}));

const Subsection = ({ children, sx }) => (
  <Grid item xs={12} sm={6} md={3} sx={{ mt: 2, px: 1, ...sx }}>
    {children}
  </Grid>
);

const Footer = ({ title }) => {
  const theme = useTheme();
  const contattaciBtnsSx = {
    px: 0,
    py: 0.5,
    mb: 0.1,
    "&:hover": { backgroundColor: "unset" },
    "&:hover > span": { color: "white.main" },
    "&:hover > div": { backgroundColor: "white.main" }
  };
  const socialBtnSx = {
    backgroundColor: "white.muted",
    ml: 1,
    "&:first-of-type": { ml: 0 },
    "& svg": { color: "secondary.muted" },
    "&:hover": { backgroundColor: "white.main" },
    "&:hover svg": { color: "secondary.main" }
  };
  return (
    <OuterWrapper>
      <InnerWrapper container sx={{px: {
    xs: theme.spacing(3),
    sm: theme.spacing(5),
    md: theme.spacing(10),
  }}}>
        <Subsection sx={{ order: 1 }}>
          <MiniTitle>Contattaci</MiniTitle>
          <Box sx={{ width: "100%" }}>
            <PhoneToBtn
              variant="secondary"
              subvariant="muted"
              label={Client.phone.replace(/(\+\d\d)\s?/i, "")}
              tel={Client.phone}
              size={1}
              justifyContent="flex-start"
              sx={contattaciBtnsSx}
            />
            <PhoneToBtn
              variant="secondary"
              subvariant="muted"
              label={Client.mobile.replace(/(\+\d\d)\s?/i, "")}
              tel={Client.mobile}
              size={1}
              justifyContent="flex-start"
              sx={contattaciBtnsSx}
            />
            <MailToBtn
              variant="secondary"
              subvariant="muted"
              size={1}
              justifyContent="flex-start"
              sx={contattaciBtnsSx}
            />
            <WhatsappToBtn
              variant="secondary"
              subvariant="muted"
              label="Whatsapp"
              size={1}
              justifyContent="flex-start"
              sx={contattaciBtnsSx}
            />
            <MapToBtn
              variant="secondary"
              subvariant="muted"
              size={1}
              justifyContent="flex-start"
              sx={contattaciBtnsSx}
            />
          </Box>
        </Subsection>

        <Grid
          item
          container
          xs={12}
          md={6}
          sx={{
            gap: 0,
            px: 1,
            order: 3,
            [theme.breakpoints.up("md")]: {
              order: 2
            }
          }}
        >
          <Grid
            item
            xs={12}
            sm={6}
            sx={{
              mt: 2,
              [theme.breakpoints.up("md")]: {
                px: 1
              }
            }}
          >
            <MiniTitle>Orari</MiniTitle>
            {Client.workingWeek.map(s => (
              <Typography key={s} sx={{ color: "white.muted", fontSize: "inherit" }}>
                {s}
              </Typography>
            ))}
          </Grid>
          <Grid
            item
            xs={12}
            sm={6}
            sx={{
              mt: 2,
              display: "flex",
              alignItems: "flex-start",
              flexDirection: "column",
              [theme.breakpoints.up("md")]: {
                px: 1
              },
              [theme.breakpoints.only("sm")]: {
                pl: "20%"
              },
              [theme.breakpoints.up("sm")]: {
                alignItems: "flex-end",
              },
            }}
          >
            <MiniTitle>Seguici</MiniTitle>
            <Box
              sx={{
                display: "flex",
                flexDirection: "row",
                // justifyContent: "flex-start"
                justifyContent: "flex-end"
              }}
            >
              <FacebookBtn variant="secondary" sx={socialBtnSx} />
              <InstagramBtn variant="secondary" sx={socialBtnSx} />
            </Box>
          </Grid>
          <Grid
            xs={12}
            item
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              color: "primary.main"
            }}
          >
            <DenteLogoIcon
              sx={{
                visibility: "hidden",
                opacity: 0,
                pt: 4,
                pb: 0,
                transition: "visibility 0s, opacity 0.5s ease-in-out",
                [theme.breakpoints.up("md")]: {
                  height: "8rem",
                  visibility: "initial",
                  opacity: 1
                },
                width: "auto"
              }}
            />
          </Grid>
        </Grid>
        <Subsection
          sx={{
            order: 2,
            display: "flex",
            alignItems: "flex-start",
            flexDirection: "column",
            [theme.breakpoints.up("md")]: {
              order: 3
            },
            [theme.breakpoints.only("sm")]: {
              pl: "20%",
            },
            [theme.breakpoints.up("sm")]: {
              alignItems: "flex-end",
            }
          }}
        >
          <MiniTitle>Novident</MiniTitle>
          {links.map(({ path, label }) => (
            <CommonButton
              key={path}
              component={RouterLink}
              variant="secondary"
              to={path}
              sx={{
                textTransform: "unset",
                display: "flex",
                justifyContent: "flex-start",
                [theme.breakpoints.up("sm")]: {
                  justifyContent: "flex-end",
                },
                fontSize: "0.85rem",
                px: 0,
                py: 0,
                width: "100%",
                // fontWeight: "bold",
                color: "white.muted",
                "&:hover": {
                  color: "white.main",
                  backgroundColor: "unset"
                }
              }}
            >
              {label}
            </CommonButton>
          ))}
        </Subsection>
        <Grid
          item
          xs={12}
          sx={{ order: 4, mt: 1, color: "white.main" }}
        >
          {Client.legalNote.map(s => (
            <Typography key={`legal-${s}`} textAlign={"center"} sx={{ color: "white.muted", fontSize: "0.75rem !important", }}>
              {s}
            </Typography>
          ))}
        </Grid>
        <Grid
          item
          xs={6}
          sm={6}
          md={9}
          sx={{ order: 4, fontSize: "0.75rem", mt: 4, color: "white.main" }}
        >
          Novident &copy; copyright 2023
        </Grid>
        <Grid
          item
          xs={6}
          sm={6}
          md={3}
          sx={{
            order: 5,
            fontSize: "0.75rem",
            mt: 4,
            color: "white.main",
            display: "flex",
            justifyContent: "flex-end"
          }}
        >
          <RouterLink
            to="/privacy-policy"
            style={{
              textDecoration: "unset",
              color: "inherit"
            }}
          >
            Privacy Policy
          </RouterLink>
        </Grid>
      </InnerWrapper>
    </OuterWrapper>
  );
};

export default Footer;
