import * as React from "react";
import { useState, useEffect } from "react";
import { Box } from "@mui/material";
import { styled } from "@mui/material/styles";
import CommonButton from "../common/CommonButton/CommonButton";
import EmailIcon from "@mui/icons-material/Email";
import PhoneIcon from "@mui/icons-material/Phone";
import LocationOnIcon from "@mui/icons-material/LocationOn";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";

import { Client } from "../../globals";

const StyledButton = styled(CommonButton)(({ theme, variant }) => ({
  backgroundColor: theme.palette[variant].main,
  paddingLeft: theme.spacing(0),
  paddingRight: theme.spacing(1),
  paddingTop: theme.spacing(0),
  paddingBottom: theme.spacing(0),
  minWidth: "32px",
  borderRadius: theme.shape.borderRadius,
  "&:hover": {
    backgroundColor: theme.palette[variant].light
  },
  "& .MuiTouchRipple-root": {
    color: theme.palette.white.main
  }
}));

const IconWrapper = styled(Box)(({ theme, variant, subvariant, fontSize }) => ({
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  minWidth: theme.typography.pxToRem(fontSize ? (fontSize * 50) / 30 : 25),
  minHeight: theme.typography.pxToRem(fontSize ? (fontSize * 50) / 30 : 25),
  borderRadius: "50%",
  backgroundColor: theme.palette.white[subvariant],
  color: theme.palette[variant][subvariant]
}));

const TextLabel = styled("span")(
  ({ theme, variant, subvariant, fontSize }) => ({
    fontSize: theme.typography.pxToRem(fontSize || 15),
    color: theme.text[variant][subvariant],
    lineHeight: "1.1rem",
    textTransform: "none",
    marginLeft: 0
  })
);

export function ContactButtonIconBaloon({
  icon,
  label,
  variant,
  subvariant,
  fontSize,
  labelFontSize,
  justifyContent,
  sx,
  ...rest
}) {
  return (
    <StyledButton
      component="a"
      variant={variant || "primary"}
      subvariant={subvariant || "main"}
      sx={{ justifyContent: justifyContent || "center", ...(sx || {}) }}
      {...rest}
    >
      <IconWrapper
        variant={variant}
        subvariant={subvariant || "main"}
        fontSize={fontSize}
      >
        {icon}
      </IconWrapper>
      <TextLabel
        variant={variant}
        subvariant={subvariant || "main"}
        fontSize={labelFontSize || fontSize}
      >
        {label}
      </TextLabel>
    </StyledButton>
  );
}

const sizes = {
  1: { iconSize: "1.175rem", fontSize: 15 },
  2: { iconSize: "1.5rem", fontSize: 20 },
  3: { iconSize: "2rem", fontSize: 25 }
};

export function ContactEmailIconBaloon({
  subject,
  body,
  size,
  label,
  ...props
}) {
  const href = `mailto:${Client.email}?subject=${encodeURIComponent(
    subject || ""
  )}&body=${encodeURIComponent(body || "")}`;

  const handleClick = (event) => {
    event.preventDefault();
    window.location.href = href;
  };

  const { iconSize, fontSize } = sizes[size || 1];

  return (
    <ContactButtonIconBaloon
      icon={<EmailIcon sx={{ fontSize: iconSize }} />}
      label={label === undefined ? Client.email : label}
      onClick={handleClick}
      href={href}
      fontSize={fontSize}
      {...props}
    />
  );
}

export function ContactPhoneIconBaloon({ label, size, tel, ...props }) {
  const href = `tel:${encodeURIComponent(tel || Client.phone)}`;

  const handleClick = (event) => {
    event.preventDefault();
    window.location.href = href;
  };
  const { iconSize, fontSize } = sizes[size || 1];

  return (
    <ContactButtonIconBaloon
      icon={<PhoneIcon sx={{ fontSize: iconSize }} />}
      label={label === undefined ? (tel || Client.phone).replace(/(\+\d\d)\s?/i, "") : label}
      onClick={handleClick}
      fontSize={fontSize}
      href={href}
      {...props}
    />
  );
}

export function ContactMapIconBaloon({ label, size, ...props }) {
  const { iconSize, fontSize } = sizes[size || 1];

  const hostAndQuery = "://www.google.com/maps/dir/?api=1&destination="
    + encodeURIComponent(Client.address.gmapsFull)
    + "&destination_place_id="
    + encodeURIComponent(Client.address.placeId)

  const [href, setHref] = useState(
    "https" + hostAndQuery
  )

  useEffect(() => {
    // should be available via gatsby-browser onInitialClientRender
    if (window.iOSVersion?.[0] > 6)
      setHref("maps" + hostAndQuery)
  }, [hostAndQuery]);

  const handleClick = (event) => {
    event.preventDefault();
    window.open(href);
  };

  return (
    <ContactButtonIconBaloon
      icon={<LocationOnIcon sx={{ fontSize: iconSize }} />}
      label={label === undefined ? Client.address.full : label}
      onClick={handleClick}
      fontSize={fontSize}
      href={href}
      {...props}
    />
  );
}

export function ContactWhatsappIconBaloon({ label, message, size, ...props }) {
  const href = (() => {
    const phoneNumber = Client.mobile.replace(/\+/g, "").replace(/ /g, "");
    return `https://wa.me/${phoneNumber}?text=${encodeURIComponent(
      message || ""
    )}`;
  })();

  const { iconSize, fontSize } = sizes[size || 1];

  const handleClick = (event) => {
    event.preventDefault();
    window.open(href);
  };

  return (
    <ContactButtonIconBaloon
      icon={<WhatsAppIcon sx={{ fontSize: iconSize }} />}
      label={label === undefined ? Client.phone : label}
      onClick={handleClick}
      fontSize={fontSize}
      href={href}
      {...props}
    />
  );
}
