// import * as React from "react";
import Button from "@mui/material/Button";
import { styled } from "@mui/material/styles";

const CommonButton = styled(Button)(({ theme }) => ({
  textDecoration: "none",
  display: "flex",
  alignItems: "center",
  padding: theme.spacing(1, 2),
  gap: theme.spacing(1)
}));

export default CommonButton;
