import * as React from "react";
import { ThemeProvider } from "@mui/material/styles";
import "./index.css";
import "@fontsource/roboto"; // Defaults to weight 400.

import { createTheme, alpha } from "@mui/material/styles";

// const primary = "rgb(68, 132, 180)"; // mateus
const primary = "rgb(64, 168, 115)";
const secondary = "rgb(72, 68, 76)";
const white = "rgb(255, 255, 255)";
const disegnoLogo = primary;
const muted = (color) => alpha(color, 0.75);
const soften = (color) => alpha(color, 0.3);

export const globalTheme = createTheme({
  breakpoints: {
    values: {
      xs: 0,
      sm: 600,
      ms: 720,
      md: 900,
      lg: 1200,
      xl: 1536,
    },
  },
  text: {
    primary: {
      main: white,
      muted: muted(white)
    },
    secondary: {
      main: white,
      muted: muted(white)
    },
    white: {
      main: secondary,
      muted: muted(secondary)
    }
  },
  palette: {
    primary: {
      main: primary,
      soft: soften(primary),
      muted: muted(primary)
    },
    secondary: {
      main: secondary,
      soft: soften(secondary),
      muted: muted(secondary)
    },
    white: {
      main: white,
      soft: soften(white),
      muted: muted(white)
    },
    brandLogo: {
      wrapper: {},
      disegnoSx: { main: disegnoLogo },
      disegnoDx: { main: disegnoLogo },
      scritta: { main: secondary }
    }
  },
  shape: { borderRadius: 30 },
  typography: {
    // h6: { color: primary },
    // h2: { color: secondary }
  }
});

const GlobalThemeProvider = ({ children }) => (<ThemeProvider theme={globalTheme}>{children}</ThemeProvider>)

export default GlobalThemeProvider
