import * as React from 'react'
import { CSSTransition } from 'react-transition-group'
import './PageTransition.css'

const PageTransition = ({ children }) => {
  return (
    <CSSTransition
      in={true}
      appear={true}
      timeout={500}
      classNames="page-transition"
    >
      {children}
    </CSSTransition>
  )
}

export default PageTransition
